import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useImmer } from "use-immer";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";
import Alert from "react-bootstrap/Alert";


export default function AccountingWithProfitsCash() {
    const { register, handleSubmit, reset } = useForm(); //, watch, errors
    const [records, updateRecords] = useImmer([]);
    const [open, setOpen] = useState(false);
    const [periods, setPeriods] = useState(["2021", "2022", "2023", "2024"]);

    const onSubmit = data => { updateRecords(draft => { draft.push(data) }); reset({ d: '...', k: '...', sum: 0 }) }; //console.log(data);

      console.log(records);

    function processRecords(indicator, periodIndex) {
        let DValues = 0;
        let KValues = 0;
        let processed = records.map(item => {
            if (item.d === indicator) { DValues = DValues + parseFloat(item.sum) }
            if (item.k === indicator) { KValues = KValues + parseFloat(item.sum) }
            return null
        })
        if (indicator === "Основные средства" || indicator === "Материалы" ||
            indicator === "Незавершенное производство" || indicator === "Готовая продукция" ||
            indicator === "Дебиторская задолженность" || indicator === "Деньги") { return DValues - KValues } else { return KValues - DValues }
    }

    return <div>
        <Container>
            <Row>
                {["Активы", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{item}</Col>)}
            </Row>
            <Row>
                {["Основные средства", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Основные средства" : processRecords("Основные средства", index)}</Col>)}
            </Row>

            <Row>
                {["Материалы", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Материалы" : processRecords("Материалы")}</Col>)}
            </Row>
            <Row>
                {["Незавершенное производство", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Незавершенное производство" : processRecords("Незавершенное производство", index)}</Col>)}
            </Row>
            <Row>
                {["Готовая продукция", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Готовая продукция" : processRecords("Готовая продукция", index)}</Col>)}
            </Row>
            <Row>
                {["Дебиторская задолженность", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Дебиторская задолженность" : processRecords("Дебиторская задолженность", index)}</Col>)}
            </Row>
            <Row>
                {["Деньги", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Деньги" : processRecords("Деньги", index)}</Col>)}

            </Row>
            <Row>
                {["Пассивы", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{item}</Col>)}
            </Row>
            <Row>
                {["Уставный капитал", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Уставный капитал" : processRecords("Уставный капитал", index)}</Col>)}
            </Row>
            <Row>
                {["Долгосрочный банковский кредит", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Долгосрочный банковский кредит" : processRecords("Долгосрочный банковский кредит", index)}</Col>)}

            </Row>
            <Row>
                {["Краткосрочный банковский кредит", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Краткосрочный банковский кредит" : processRecords("Краткосрочный банковский кредит", index)}</Col>)}
            </Row>
            <Row>
                {["Кредиторская задолженность", ...periods].map((item, index) => <Col key={item} xs={index > 0 ? 2 : 4}>{index === 0 ? "Кредиторская задолженность" : processRecords("Кредиторская задолженность", index)}</Col>)}
            </Row>
        </Container>
        <hr />
        <Button
            onClick={() => setOpen(!open)}
            aria-controls="example-collapse-text"
            aria-expanded={open}
            variant="outline-secondary"
            className="mb-3"
        >
            {open ? "Скрыть Журнал" : "Показать журнал"}
        </Button>
        <Collapse in={open}>
            <div id="example-collapse-text">
                <Container>
                    {records.map((row, index) => <Row key={index}>
                        <Col>{row.d}</Col>
                        <Col>{row.k}</Col>
                        <Col>{row.sum}</Col>
                    </Row>)}
                </Container>
            </div>
        </Collapse>
        <hr />
        <form onSubmit={handleSubmit(onSubmit)}>
            <Form.Row>
                <Form.Group as={Col} controlId="formStatePeriod">
                    <Form.Label>Период</Form.Label>
                    <Form.Control as="select" name="period" ref={register} required>
                        {["...", ...periods].map(item => <option key={item}>{item}</option>)}
                    </Form.Control>
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group as={Col} controlId="formStateD">
                    <Form.Label>Д</Form.Label>
                    <Form.Control as="select" name="d" ref={register} required>
                        <option>...</option>
                        <option>Основные средства</option>
                        <option>Материалы</option>
                        <option>Незавершенное производство</option>
                        <option>Готовая продукция</option>
                        <option>Дебиторская задолженность</option>
                        <option>Деньги</option>
                        <option>Уставный капитал</option>
                        <option>Нераспределенная прибыль</option>
                        <option>Долгосрочный банковский кредит</option>
                        <option>Краткосрочный банковский кредит</option>
                        <option>Кредиторская задолженность</option>
                    </Form.Control>
                </Form.Group>
                <Form.Group as={Col} controlId="formStateK">
                    <Form.Label>К</Form.Label>
                    <Form.Control as="select" name="k" ref={register} required>
                        <option>...</option>
                        <option>Основные средства</option>
                        <option>Материалы</option>
                        <option>Незавершенное производство</option>
                        <option>Готовая продукция</option>
                        <option>Дебиторская задолженность</option>
                        <option>Деньги</option>
                        <option>Уставный капитал</option>
                        <option>Нераспределенная прибыль</option>
                        <option>Долгосрочный банковский кредит</option>
                        <option>Краткосрочный банковский кредит</option>
                        <option>Кредиторская задолженность</option>
                    </Form.Control>
                </Form.Group>
                <Form.Group as={Col} controlId="formStateSum">
                    <Form.Label>Сумма</Form.Label>
                    <Form.Control as="input" name="sum" ref={register} required />
                </Form.Group>
            </Form.Row>

            <Button variant="outline-secondary" type="submit" >Провести операцию</Button>
        </form>
    </div >
}